
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
.clickable {
  cursor: pointer;
}
/* banner */
.hero {
  height: auto; /* grows according to text - won't need updating if I move the instructions*/
  background-image: url(/static/img/ffb-banner.b908899.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: white; /* text color */
}
/* flex */
.parent {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: wrap;
      -ms-flex: wrap;
          flex: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.child {
  margin: 1em;
}
.child rankings {
  -webkit-box-flex: 2;
      -ms-flex: 2 2 auto;
          flex: 2 2 auto;
}
.child rbs {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1; /* equivalent of flex: 1 1 auto */
}
.child wrs {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
/* NOTE: no child class for QB & TE because they share the width of the tables above them */
.child drafted {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.orange {
  border: none;
  padding: 0 0 0 5px;
  color: orange;
  font-weight: bold;
}
.rankingsTable {
  display: block;
  height: 1000px;
  overflow-y: hidden;
}
.posTable {
  display: block;
  overflow: auto;
  height: 300px;
  width: 250px;
}
.draftedTable {
  width: 250px;
}
/* account for gigantic names */
.posTable tbody tr td:first-child {
  width: 90%;
}
/* .draftedTable tbody tr td:first-child {
  width: 90%;
} */
.search {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.search results {
  min-height: 100px;
}
table, tr, td {
  border: 1px solid black;
  border-collapse: collapse;
  background-color: #ffffff; /* keep this in case the page bg color is changed */
}
th {
  font-weight: normal;
}
td {
  padding: 1px 2px 1px 2px;
}
tr:hover {
  background-color: #f5f5f5;
}

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.upload-button {
  position: relative;
  overflow: hidden;
  display: inline-block;
  color: orange;

  border: 2px solid black;
  border-radius: 5px;
  padding: 8px 12px;
  cursor: pointer;
}
.upload-button:hover {
  background-color: #f5f5f5;
}
.upload-button input {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 0;
}
